import React from 'react';

import './_team-stats.scss';

class TeamStats extends React.Component {
	render() {
		var bgStyleA = {
      backgroundImage:`url(${this.props.data.logo})`
    };
		return (<div className="card mb-3 view-head">
		<div className="card-body">
			<div className="row">
			<div className="col-lg-4">
				<div id="teamHeadSelector">
					<div className="image" style={ bgStyleA }></div>
						 
							<select className="form-control" id="teamSelector" onChange={this.props.change}>
								{this.props.teams.map((team, index) => {
									return <option key={'key'+index} value={team}>{team}</option>
								})}
							</select>
						 
				</div>
				 <p>
					<strong>Founded: </strong>{this.props.data.founded}<br />
					<strong>Seasons: </strong> {this.props.data.seasons} <br />
					<strong>Record: </strong> <span id="Record">?</span> <br />
					<strong>Current Streak: </strong> {this.props.data.stats.currentStreak} <br />
					<strong>Biggest Winning Streak: </strong> <span id="big-win-streak">{this.props.data.stats.highestStreak}</span> <br />
					<strong>Biggest Losing Streak: </strong> <span id="big-loss-streak">{this.props.data.stats.lowestStreak}</span> <br />
							<strong>Total Points: </strong><span id="total-points">{this.props.data.stats.totalPoints.toFixed(2)}</span><br />
							<strong>Total Points Against: </strong><span id="total-points-against">{this.props.data.stats.totalPointsAgainst.toFixed(2)}</span><br />
				</p>
			</div> 
			<div className="col-lg-4">
				<p>
					<strong>Average Points: </strong><span id="avg-points">{this.props.data.averageScore.toFixed(2)}</span><br />
					<strong>Average Points Against: </strong><span id="avg-points-against">{this.props.data.averageScoreAgaints.toFixed(2)}</span><br />
							<strong>Highest Score: </strong><span id="highest-score">{this.props.data.stats.highestScore.toFixed(2)}</span><br />
							<strong>Lowest Score: </strong><span id="loweset-score">{this.props.data.stats.lowestScore.toFixed(2)}</span><br />
							<strong>Biggest Win Margin: </strong><span id="big-win-margin">{this.props.data.stats.highestMargin.toFixed(2)}</span><br />
					<strong>Closest Game: </strong><span id="closet-game">{this.props.data.stats.lowestMargin.toFixed(2)}</span><br />
				</p>
			</div>
			<div className="col-lg-4">
				<div >
				 
					<p>
						
						<strong>Nickname: </strong> <span id="nickname">{this.props.data.nickname}</span> <br />
						<strong>Experience: </strong> <span id="experience">{this.props.data.experience}</span> <br />
						<strong>Earnings: </strong> <span id="earnings">{this.props.data.earnings}</span> <br />
					</p>
				</div>
			</div>
		</div>
		</div> 
	</div>);
	}

}
   
          
          
export default TeamStats;


